import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';

import WithTooltip from '../tooltip/WithTooltip';
import TooltipIgnore from './TooltipIgnore';

const Rect = styled.rect`
  transition: all 150ms ease-out;
  transform: translate3d(0, 0, 0);
  fill: rgb(255, 255, 0);
  fill-opacity: 0.4;

  &:hover {
    transform: translate3d(0, -1px, 0);
    fill: rgb(255, 245, 0);
    fill-opacity: 0.6;
  }

  &:active {
    transform: translate3d(0, 0, 0);
  }
`;

export default function IgnoredRegions({
  imageSize: { width: imageWidth, height: imageHeight },
  ignoredRegions,
  ...props
}) {
  const rects = ignoredRegions
    .map(({ selector, regions }) =>
      regions.map(({ top, left, width, height }) => (
        <WithTooltip
          svg
          key={`${selector}-${top}-${left}-${width}-${height}`}
          placement="top"
          mode="click"
          tooltip={<TooltipIgnore selector={selector} />}
        >
          <Rect x={left} y={top} width={width} height={height} />
        </WithTooltip>
      ))
    )
    .reduce((a, b) => [...a, ...b], []);

  return (
    <svg
      viewBox={`0 0 ${imageWidth} ${imageHeight}`}
      width={imageWidth}
      height={imageHeight}
      {...props}
    >
      {rects}
    </svg>
  );
}

IgnoredRegions.propTypes = {
  imageSize: PropTypes.shape({
    width: PropTypes.number.isRequired,
    height: PropTypes.number.isRequired,
  }).isRequired,
  ignoredRegions: PropTypes.arrayOf(
    PropTypes.shape({
      selector: PropTypes.string.isRequired,
      regions: PropTypes.arrayOf(
        PropTypes.shape({
          top: PropTypes.number.isRequired,
          left: PropTypes.number.isRequired,
          width: PropTypes.number.isRequired,
          height: PropTypes.number.isRequired,
        }).isRequired
      ),
    })
  ).isRequired,
};



